import React from 'react'
import { MdExpandMore } from 'react-icons/md'
import styled from 'styled-components'
import getWindowWidth from '../../helpers/getWindowWidth'

const ShowMore = ({
    handleClick,
}: {
    handleClick: (
        event: React.MouseEvent<HTMLButtonElement, MouseEvent>
    ) => void
}) => (
    <HorizontalLine>
        <ShowMoreBtn title='Show more projects' onClick={handleClick}>
            <ExpandMoreIcon />
            <ExpandMoreIcon />
        </ShowMoreBtn>
    </HorizontalLine>
)

export const HorizontalLine = styled.div`
    height: 0;
    width: 80%;
    margin: 70px auto 0;
    height: 2px;
    border: none;
    background-image: linear-gradient(
        to right,
        ${({ theme }) => theme.colors.secondary} -20%,
        ${({ theme }) => theme.colors.primary} 50%,
        ${({ theme }) => theme.colors.secondary} 120%
    );
    position: relative;
`
export const ShowMoreBtn = styled.button`
    display: flex;
    flex-direction: column;
    background: none;
    border: none;
    margin: 0 auto;
    background: white;
    border-radius: 50%;
    width: 53px;
    align-items: center;
    border: 2px solid #f4b266;
    position: absolute;
    top: -25px;
    left: ${((4 / 5) * getWindowWidth()) / 2 - 26.5}px;
    cursor: pointer;
`
export const ExpandMoreIcon = styled(MdExpandMore)`
    width: 2.5rem;
    height: 2.5rem;
    color: #834600;
    &:last-of-type {
        margin-top: -31px;
    }
`
export default ShowMore
